%h1 {
  font-size: 48px;
  font-weight: 800;
  line-height: 56px;
  letter-spacing: -0.01em;
}

%h2 {
  font-size: 32px;
  font-weight: 800;
  line-height: 42px;
  letter-spacing: -0.01em;
}

%h3 {
  font-size: 22px;
  font-weight: 800;
  line-height: 32px;
}

%h4 {
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
}

%uppercase {
  font-size: 12px;
  font-weight: 700;
  line-height: 11px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

%buttons {
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
}

%body-text {
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
}

%small-text {
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
}
